___CSS_LOADER_EXPORT___.push([module.id, `*,
::before,
::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.15;
  -webkit-font-smoothing: antialiased;
  font-family: system-ui, "Segoe UI", Roboto, Arial;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

svg {
  vertical-align: baseline;
}

input,
button,
textarea,
select,
optgroup {
  font: inherit;
  font-size: 100%;
  line-height: 1.15;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

:root {
  --bgc-0: #171717;
  --bgc-1: #525252;
  --bgc-2: #d4d4d4;
  --bgc-3: #fafafa;

  --shadow-c: #262626;

  --button-bg: #e0f2fe;
  --button-border: #0ea5e9;
  --button-c: #082f49;

  background-color: var(--bgc-0);
  font-size: 1.25rem;
}

.box {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 1rem;
  border: 2px solid black;
  box-shadow: 3px 3px 5px var(--shadow-c);
  align-items: center;
  border-radius: 5px;
  background-color: var(--bgc-2);

  > .box {
    background-color: var(--bgc-3);
    border-radius: 10px;
  }

  &:hover {
    transform: scale(1.1);
  }
}

.box-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 1rem;
  align-items: center;
  min-height: 100vh;
  max-width: 80%;
  margin-inline: auto;
  background-color: var(--bgc-1);
  border-radius: 3px;
}

button {
  background-color: var(--button-bg);
  border: 2px solid var(--button-border);
  color: var(--button-c);
}

textarea:focus {
  min-width: 200px;
  min-height: 150px;
}

fieldset {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  box-shadow: 2px 2px 3px var(--shadow-c);
  border-radius: 3px;

  > legend {
    font-weight: 600;
  }

  > div {
    display: flex;
    gap: 0.4rem;
    justify-content: center;
  }
}

#response {
  white-space: pre;
  background-color: var(--bgc-3);
  font-size: 1.35rem;
  padding: 2rem;
}

#trees {
  > div {
    white-space: pre;
    line-height: 1.35;
  }

  b {
    font-weight: 600;
  }
}
